import React from 'react'

const donwloadOptions = () => {
    return(
        <div className="modal fade" id="download-options" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div className="blur-bg" />
            <div className="modal-dialog modal-dialog-centered" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalCenterTitle">Download</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <ul className="list-group">
                            <li className="list-group-item">
                                <ul style={{listStyleType: "disc", float: "left"}}>
                                    <li>Windows 7+</li>
                                    <li>1 GB RAM</li>
                                    <li>Intel Core 2 Duo</li>
                                </ul>
                                <a  href="https://github.com/mttv/mttv-app/releases/download/v0.2.0/mttv-setup-0.2.0.exe" 
                                    target="_blank" 
                                    rel="noopener noreferrer"
                                    download
                                    className="btn btn-primary btn-download"><i className="fab fa-windows icon"></i>Download</a>
                            </li>
                            <li className="list-group-item">
                                <ul style={{listStyleType: "disc", float: "left"}}>
                                    <li>Mac OSX 10.10+</li>
                                    <li>Intel Core 2 Duo</li>
                                </ul>
                                <a  href="https://github.com/mttv/mttv-app/releases/download/untagged-3021bd9c75b998ddac0c/mttv-setup-0.2.0.exe" 
                                    target="_blank" 
                                    rel="noopener noreferrer"
                                    download
                                    disabled
                                    className="btn btn-primary btn-download disabled"><i className="fab fa-apple icon"></i>Download</a>
                            </li>
                            <li className="list-group-item"><a href="https://github.com/mttv/mttv-app/releases" target="_blank" rel="noopener noreferrer">Download manually</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default donwloadOptions